@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&display=swap');

body{
 background-image: url("./components/media/stars.jpg");
    color: rgb(255, 255, 255);
    font-family: 'Roboto Mono', monospace;
    
}

.card{
    height: 330px;
    width: 200px;
 
}

.titleGame {
    text-align: center;
    text-shadow: black 0.1em 0.1em 0.2em;
}

.wrapCard:hover{
    background-color: rgba(255, 255, 255, 0.644);
}

.wrapCard{
    display: flex;
    flex-direction: column;
    border: 2px solid white;
    border-radius: 7px;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.315);
    font-weight: bold;
    color: rgb(0, 0, 0);
    transition-duration: 0.5s;
    text-shadow: -2px 0 rgb(255, 255, 255), 0 1px rgb(255, 255, 255), 1px 0 rgb(255, 255, 255), 0 -1px rgb(255, 255, 255);
}

.cardName {
    margin-top: 5px;
}

.cardsContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    flex-wrap: wrap;
    height: 500px;
    width: 100%;
}

.logo {
    height: 20vh;
    display: flex;
    margin: auto;
    margin-bottom: 2vh;
}

.wrapCard:hover {
    cursor: pointer;
}

